import styled from 'styled-components';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import img3 from './images/img3.jpg';

const SinglePostContainer = styled.article`
    flex: 9;
    font-family: 'Lora', serif;
`
const SinglePostWrapper = styled.div`
    padding: 20px;
    padding-right: 0;
`
const SinglePostImg = styled.img`
    width: 100%;
    height: 300px;
    border-radius: 5px;
    object-fit: cover; 
`
const SignlePostTitle = styled.h1`
    text-align: center;
    font-size: 1.6rem;
`
const EditDeleteWrapper = styled.div`
    float: right;
    font-size: 1rem;
    >svg {
        margin-left: 10px;
        cursor: pointer;
        &:first-child {
           color: teal; 
        }
        &:last-child {
           color: tomato; 
        }
    }
`
const SinglePostInfo = styled.div`
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    color: #b39656;
    font-family: 'Varela Round', sans-serif;
`
const PostText = styled.p`
    color: #666;
    font-size: 1.1rem;
    line-height: 1.5;
    &::first-letter {
        margin-left: 20px;
        font-size: 1.5rem;
        font-weight: 600;
    }
`

export default function SinglePost() {
    return (
        <SinglePostContainer>
            <SinglePostWrapper>
                <SinglePostImg src={img3} />
                <SignlePostTitle>Lorem ipsum dolor sit amet.
                    <EditDeleteWrapper>
                        <EditIcon />
                        <DeleteIcon />
                    </EditDeleteWrapper>
                </SignlePostTitle>
                <SinglePostInfo>
                    <span>Author: <b>CodMitu</b></span>
                    <span>1 hour ago</span>
                </SinglePostInfo>
                <PostText>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Quis magnam obcaecati cumque tempore quos adipisci aliquam voluptate, quasi vero fugiat, expedita at! Distinctio, officia accusamus molestiae porro itaque reprehenderit maiores saepe vitae tempora est nobis quisquam consequatur ratione ut ex sapiente ipsa laboriosam id provident necessitatibus assumenda! Iure, repellendus reiciendis voluptatibus veniam quasi quis assumenda nesciunt voluptas facere libero, ab dicta ipsum repudiandae adipisci, at recusandae animi? Totam consequuntur sequi sed molestias eveniet culpa rerum explicabo facere corrupti praesentium ab error voluptas, ullam maiores consectetur repudiandae harum cumque saepe, ratione aut vel blanditiis. Sapiente, possimus commodi id reprehenderit et itaque nobis ducimus debitis, adipisci labore consequatur dignissimos voluptatem, assumenda veniam obcaecati! Nobis sequi culpa sed debitis enim, velit reiciendis recusandae. Magnam expedita a deleniti alias eum, nostrum quas necessitatibus optio explicabo, magni aliquam reiciendis, saepe ad! Cum incidunt, nam rerum dolorum praesentium qui sequi nulla animi necessitatibus nobis quibusdam suscipit quasi eligendi impedit. Tempore, in laudantium recusandae possimus vel aspernatur dolorem consectetur blanditiis itaque enim obcaecati provident dolorum iste a minus veritatis eos sint beatae accusantium dolor numquam. Molestiae, nam quod? Nesciunt modi obcaecati beatae nulla quidem libero provident maiores asperiores accusantium omnis? Aperiam temporibus, earum eligendi necessitatibus saepe non!</PostText>
            </SinglePostWrapper>
        </SinglePostContainer>
    )
}
