import styled from 'styled-components';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import PinterestIcon from '@material-ui/icons/Pinterest';
import InstagramIcon from '@material-ui/icons/Instagram';
import profileImg from './images/profile-img.png';
import noUser from './images/no-user.jpg';
import SearchIcon from '@material-ui/icons/Search';
import { Link } from "react-router-dom";

const TopbarContainer = styled.nav`
    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    z-index: 10;
`
const LeftWrapper = styled.div`
    flex: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    >svg {
        font-size: 1.2rem;
        margin-right: 10px;
        color: #444;
        cursor: pointer;
    }
`
const CenterWrapper = styled.div`
    flex: 6;
`
const ListWrapper = styled.div`
    display: flex;
    justify-content: center;
    margin:0;
    padding: 0;
`
const ListLink = styled(Link)`
    margin-right: 20px;
    font-size: 1.1rem;
    font-weight: 300;
    color: black;
    cursor: pointer;
    &:hover {
        color: #bc7340;
    }
`
const SearchSymbol = styled(SearchIcon)`
    font-size: 1.1rem;
    color: #666;
    cursor: pointer;
    margin-left: 15px;
`
const RightWrapper = styled.div`
    flex: 3;
    display: flex;
    align-items: center;
`
const ProfileImg = styled.img`
    top: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
`

export default function TopBar({user}) {
    return (
        <TopbarContainer>
            <LeftWrapper>
                <FacebookIcon />
                <TwitterIcon />
                <PinterestIcon />
                <InstagramIcon />
            </LeftWrapper>
            <CenterWrapper>
                <ListWrapper>
                    <ListLink to="/">HOME</ListLink>
                    <ListLink to="/about">ABOUT</ListLink>
                    <ListLink to="/contact">CONTACT</ListLink>
                    <ListLink to="/write">WRITE</ListLink>
                    {
                        user ?
                        <ListLink to="/">LOGOUT</ListLink>
                        :
                            <>
                                <ListLink to="/login">LOGIN</ListLink>
                                <ListLink to="/signup">SIGNUP</ListLink>
                            </>

                    }
                </ListWrapper>
            </CenterWrapper>
            <RightWrapper>
                {
                    user ?
                    <ProfileImg src={profileImg} alt="profile image" />
                    : <ProfileImg src={noUser} alt="profile image" />
                }
                <SearchSymbol />
            </RightWrapper>
        </TopbarContainer>
    )
}
