import styled from 'styled-components';
import Header from '../Header';
import Posts from '../Posts';
import SideBar from '../SideBar';

const HomeContainer = styled.main`
    display: flex;
`

export default function Homepage() {
    return (
        <>
            <Header />
            <HomeContainer>
                <Posts />
                <SideBar />
            </HomeContainer>
        </>
    )
}
