import './App.css';
import Homepage from './components/pages/Homepage';
import Singlepage from './components/pages/Singlepage';
import Writepage from './components/pages/Writepage';
import Loginpage from './components/pages/Loginpage';
import Contactpage from './components/pages/Contactpage';
import Settingspage from './components/pages/Settingspage';
import Aboutpage from './components/pages/Aboutpage';
import Signuppage from './components/pages/Signuppage';
import TopBar from './components/TopBar';
import { BrowserRouter as Router, Switch, Link, Route } from 'react-router-dom';

function App() {
  const user = false;

  return (
      <Router basename="/blog-website">
        <TopBar user={user} />
        <Switch>
          <Route path="/" exact component={Homepage}/>
          <Route path="/signup" component={user ? Homepage : Signuppage}/>
          <Route path="/login" component={user ? Homepage : Loginpage}/>
          <Route path="/about" component={user ? Settingspage : Aboutpage}/>
          <Route path="/contact" component={Contactpage}/>
          <Route path="/write" component={user ? Writepage : Loginpage}/>
          {/* <Route path="/settings" component={user ? Settingspage : Loginpage}/> */}
          <Route path="/post" component={Singlepage}/>
        </Switch>
      </Router>
  );
}

export default App;
