import styled from 'styled-components';
import img3 from './images/img3.jpg';

const PostContainer = styled.article`
    /* width: 385px; */
    margin: 0 25px 40px 25px;
    font-family: 'Varela Round', sans-serif;
    flex: 1 1 320px;
`
const PostImg = styled.img`
    width: 100%;
    object-fit: cover;
    height: 280px;
    border-radius: 7px;
`
const PostInfo = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`
const PostCategWrapper = styled.div`
    
`
const PostCateg = styled.span`
    font-size: 0.8rem;
    color: #be9656;
    line-height: 2;
    margin-top: 15px;
    margin-right: 10px;
    cursor: pointer;
`
const PostTitle = styled.span`
    font-family: 'Josefin Sans', sans-serif;
    font-size: 1.5rem;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
`
const PostDate = styled.span`
    font-style: italic;
    font-size: 0.9rem;
    color: #999;
    margin-top: 15px;
    font-family: 'Lora', serif;
`
const PostDescription = styled.p`
    font-size: 0.9rem;
    color: #444;
    line-height: 1.6;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
`


export default function Post() {
    return (
        <PostContainer>
            <PostImg src={img3} alt="by stein egil liland from Pexels" title="by stein egil liland from Pexels" />
            <PostInfo>
                <PostCategWrapper>
                    <PostCateg>Music</PostCateg>
                    <PostCateg>Life</PostCateg>
                </PostCategWrapper>
                <PostTitle>Lorem ipsum dolor sit.</PostTitle>
                <hr />
                <PostDate>1 hour ago</PostDate>
            </PostInfo>
            <PostDescription>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Incidunt obcaecati culpa explicabo neque asperiores assumenda consequuntur impedit molestiae illum voluptates ad voluptas dolores, eaque tempora, necessitatibus ipsum at ea dolore. Lorem ipsum dolor sit amet consectetur adipisicing elit. Vitae, aut eum, ipsum aliquam sapiente libero, sint corporis ipsa optio cumque delectus quod et fugit atque. Accusantium doloremque aliquid consequatur libero error, provident, eaque molestias repellat, quibusdam vero sapiente saepe hic adipisci? Ad nam ratione pariatur perspiciatis maxime tempore porro fugiat ut corrupti, amet quasi quidem sit tempora nostrum? Assumenda, laudantium.
            </PostDescription>
        </PostContainer>
    )
}
