import styled from 'styled-components';
import SideBar from '../SideBar';
import aboutImg from '../images/about-img.jpg';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import EditIcon from '@material-ui/icons/Edit';

const SettingsContainer = styled.section`
    display: flex;
    font-family: 'Lora', serif;
`
const SettingsWrapper = styled.section`
    flex: 9;
    padding: 20px;
`
const SettingsTitle = styled.span`
    display: flex;
    align-items: center;
    justify-content: space-between;
`
const UpdateTitle = styled.span`
    font-size: 1.8rem;
    margin-bottom: 20px;
    color: lightcoral;
`
const DeleteTitle = styled.span`
    font-size: 0.8rem;
    cursor: pointer;
    color: crimson;
`
const FormWrapper = styled.form`
    display: flex;
    flex-direction: column;
`
const ImageWrapper = styled.div`
    display: flex;
    align-items: center;
    margin-top: 10px;
    margin: 10px 0;
`
const ProfileImage = styled.img`
    width: 70px;
    height: 70px;
    border-radius: 20px;
    object-fit: cover;
`
const ProfileIcon = styled(AccountCircleIcon)`
    width: 25px;
    height: 25px;
    display: grid;
    place-items: center;
    color: white;
    background-color: lightcoral;
    border-radius: 50%;
    cursor: pointer;
    margin-left: 10px;
`
const Label = styled.label`
    font-size: 1.3rem;
    margin-top: 20px;
`
const Input = styled.input`
    color: grey;
    margin: 10px 0;
    height: 30px;
    border: none;
    border-bottom: 1px solid lightgray;
`
const Textarea = styled.textarea`
    color: grey;
    margin: 10px 0;
    height: 200px;
    border: none;
    border-bottom: 1px solid lightgray;
`
const InputFile = styled.input`
    display: none;
`
const useStyles = makeStyles({
    button: {
        marginTop: 20,
        width: 150,
        alignSelf: 'center',
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 10,
        border: 0,
        color: 'white',
        height: 48,
        padding: '0 30px',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    },
});

export default function Settingspage() {
    const classes = useStyles();

    return (
        <SettingsContainer>
            <SettingsWrapper>
                <SettingsTitle>
                    <UpdateTitle>Update Your Account</UpdateTitle>
                    <DeleteTitle>Delete Your Account</DeleteTitle>
                </SettingsTitle>
                <FormWrapper>
                    <label>Profile Picture</label>
                    <ImageWrapper>
                        <ProfileImage src={aboutImg} />
                        <label htmlFor="fileInput">
                            <ProfileIcon />
                        </label>
                    </ImageWrapper>
                    <InputFile type="file" id="fileInput" />
                    <Label>Username</Label>
                    <Input type="text" placeholder="CodMitu" />
                    <Label>Email</Label>
                    <Input type="email" placeholder="codmitu@codmitu.com" />
                    <Label>Password</Label>
                    <Input type="password" />
                    <Label>About ME</Label>
                    <Textarea></Textarea>
                    <Button
                        classes={{ root: classes.button }}
                        endIcon={<EditIcon />}
                    >
                        Update
                    </Button>
                </FormWrapper>
            </SettingsWrapper>
            <SideBar />
        </SettingsContainer>
    )
}
