import styled from 'styled-components';
import abtImg from './images/about-img.jpg';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import PinterestIcon from '@material-ui/icons/Pinterest';
import InstagramIcon from '@material-ui/icons/Instagram';

const SidebarContainer = styled.aside`
    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
`
const SidebarItem = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
`
const SidebrTitle = styled.span`
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round', sans-serif;
    font-size: 0.8rem;
    color: #222;
    font-weight: 600;
    line-height: 2;
    text-align: center;
`
const SidebarImg = styled.img`
    margin-top: 15px;
    width: 70%;
    aspect-ratio: 1/1.2;
    object-fit: cover;
    object-position: top 20% left 0%;
`
const SidebarText = styled.p`
    padding: 30px;
    font-family: 'Lora', serif;
`
const ListWrapper = styled.ul`
    list-style: none;
    margin-bottom: 30px;
`
const ListItem = styled.li`
    display: inline-block;
    width: 50%;
    margin-top: 15px;
    cursor: pointer;
`
const SocialsWrapper = styled.div`
    margin-top: 15px;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    >svg {
        font-size: 1rem;
        margin-left: 10px;
        cursor: pointer;
    }
`

export default function SideBar() {
    return (
        <SidebarContainer>
            <SidebarItem>
                <SidebrTitle>ABOUT ME</SidebrTitle>
                <SidebarImg src={abtImg} alt="by Thuanny Gantuss from Pexels" title="by Thuanny Gantuss from Pexels" />
                <SidebarText>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Voluptatibus praesentium magnam qui animi quas nostrum laboriosam perspiciatis.</SidebarText>
            </SidebarItem>
            <SidebarItem>
                <SidebrTitle>CATEGORIES</SidebrTitle>
                <ListWrapper>
                    <ListItem>Life</ListItem>
                    <ListItem>Music</ListItem>
                    <ListItem>Style</ListItem>
                    <ListItem>Sport</ListItem>
                    <ListItem>Tech</ListItem>
                    <ListItem>Cinema</ListItem>
                </ListWrapper>
            </SidebarItem>
            <SidebarItem>
                <SidebrTitle>FOLLOW US</SidebrTitle>
                <SocialsWrapper>
                    <FacebookIcon />
                    <TwitterIcon />
                    <PinterestIcon />
                    <InstagramIcon />
                </SocialsWrapper>
            </SidebarItem>
        </SidebarContainer>
    )
}
