import styled from "styled-components";
import Post from "./Post";

const PostsContainer = styled.article`
    flex: 9;
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
`

export default function Posts() {
    return (
        <PostsContainer>
            <Post />
            <Post />
            <Post />
            <Post />
            <Post />
            <Post />
            <Post />
        </PostsContainer>
    )
}
