import styled from "styled-components";
import { Link } from "react-router-dom";
import loginImg from '../images/login-img.jpg';
import { Button } from "@material-ui/core";

const SigninContainer = styled.div`
font-family: 'Lora', serif;
    height: calc(100vh - 50px);
    background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5)), url(${loginImg}) no-repeat center / cover;
`
const FormWrap = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 400px) {
        height: 80%;
    }
`

const FormContent = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @media screen and (max-width: 480px) {
        padding: 10px;
    }
`
const Form = styled.form`
    height: auto;
    width: 100%;
    z-index: 1;
    display: grid;
    margin: 0 auto;
    border-radius: 5px;
    @media screen and (max-width:400px) {
        padding: 2rem;
    }
`
const FormH1 = styled.h1`
    margin-bottom: 40px;
    color: white;
    font-size: 3rem;
    font-weight: 400;
    text-align: center;
`
const FormLabel = styled.label`
    margin-bottom: 8px;
    font-size: 14px;
    color: white;
`
const FormInput = styled.input`
    padding: 1rem;
    margin-bottom: 2rem;
    border: none;
    border-radius: 5px;
`
const LinksWrapper = styled.div`
    width: 100%;
    text-align: center;
    margin-top: 24px;
`
const Text = styled(Link)`
    color: white;
    font-size: 14px;
    margin: 0 10px;
`

export default function SignInpage() {
    return (
        <SigninContainer>
            <FormWrap>
                <FormContent>
                    <FormH1>Login</FormH1>
                    <Form>
                        <FormLabel >Email</FormLabel>
                        <FormInput type='email' required placeholder="Enter your email" />
                        <FormLabel >Password</FormLabel>
                        <FormInput type="password" required placeholder="Enter your password" />
                        <Button type="submit" color="secondary" variant="contained">Login</Button>
                        <LinksWrapper>
                            {/* <Text to="/signup">Sign Up</Text> */}
                            <Text to="/forgotpass">Forgot password?</Text>
                        </LinksWrapper>
                    </Form>
                </FormContent>
            </FormWrap>
        </SigninContainer >
    )
}
