import {useState} from 'react';
import styled from 'styled-components';
import ImageIcon from '@material-ui/icons/Image';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import PostAddIcon from '@material-ui/icons/PostAdd';
import img1 from '../images/img1.jpg';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';

const WriteContainer = styled.section`
    padding-top: 50px;
`
const Image = styled.img`
    margin: 0 auto;
    display: block;
    width: 70vw;
    height: 250px;
    border-radius: 10px;
    object-fit: cover;
    object-position: top 40% left 0;
`
const FormWrapper = styled.form`
    display: flex;
    align-items: center;
    width: 70vw;
    margin: 50px auto;
`
const FormGroup = styled.div`
    margin-left: 150px;
`
const Label = styled.label`
    align-self: center;
    >svg {
        margin: 0 auto;
        margin-top: 10px;
        width: 40px;
        height: 40px;
        padding: 5px;
        border: 2px solid;
        display: grid;
        place-items: center;
        font-size: 1.3rem;
        color: grey;
        cursor: pointer;
        &:hover {
            border-color: #FE6B8B;
        }
    }
`
const InputFile = styled.input`
    display: none;
`
const Input = styled.input`
    font-size: 1.6rem;
    border: none;
    padding: 20px;
    width: 100%;
`
const Textarea = styled.textarea`
    width: 70vw;
    font-size: 1.3rem;
    height: 100vh;
    border: none;
    margin-top: 20px;
`
const useStyles = makeStyles({
    button: {
        background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        height: 48,
        padding: '0 30px',
        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
    }
});


export default function Writepage() {
    const classes = useStyles();

    const [age, setAge] = useState('');

    const handleChange = (event) => {
      setAge(event.target.value);
    };

    return (
        <WriteContainer>
            <Image src={img1}  />
            <FormWrapper>
                <Grid container spacing={1}>
                    <Grid item xs={1}>
                        <Label htmlFor="fileInput" title="Choose an image">
                            <ImageIcon/>
                        </Label>
                        <InputFile type="file" id="fileInput" />
                    </Grid>
                    <Grid item xs={2}>
                        <FormControl fullWidth={true}>
                            <InputLabel id="categories">Category</InputLabel>
                            <Select
                            required
                            labelId="categories"
                            value={age}
                            onChange={handleChange}
                            autoWidth='100%'
                            >
                                <MenuItem value="">
                                    <em>None</em>
                                </MenuItem>
                                <MenuItem value='life'>Life</MenuItem>
                                <MenuItem value='music'>Music</MenuItem>
                                <MenuItem value='style'>Style</MenuItem>
                                <MenuItem value='sport'>Sport</MenuItem>
                                <MenuItem value='tech'>Tech</MenuItem>
                                <MenuItem value='cinema'>Cinama</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item xs={7}>
                        <TextField fullWidth={true} required label="Title" />  
                    </Grid>
                    <Grid item xs={2}>
                    <Button
                        classes={{ root: classes.button }}
                        endIcon={<PostAddIcon />}
                    >
                        Publish
                    </Button>
                    </Grid>
                    <Grid item xs={12}>
                        <Textarea placeholder="Tell your story..."></Textarea>
                    </Grid>
                    
                    </Grid>
            </FormWrapper>
        </WriteContainer >
    )
}
